body {
  background: linear-gradient(to bottom, rgb(28, 28, 28), rgb(20, 20, 20));
}

.about-container {
  padding: 10vh 20rem 1rem;
  display: flex;
  justify-content: center;
  align-items: center;
  height: auto;
  color: rgb(187, 187, 187);
}

.about {
  display: flex;
  flex-direction: column;
  padding: 2rem;
  margin: 10rem 0 15rem;
  border-radius: 12px;
  background-color: rgb(28, 28, 28);
}

.about-content {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 2rem;
  padding: 0 5rem;
  max-width: 950px;
}

.about-content img {
  width: 100%;
  max-width: 250px;
  height: auto;
  object-fit: contain;
  align-self: flex-start;
  padding-top: 2rem;
}

.about-content p {
  padding-top: 2rem;
  font-size: 120%;
}

@media (max-width: 2000px) {
  .about-container {
    padding: 8vh 10rem 1rem;
  }

  .about-content {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .about-content img {
    align-self: center;
  }
}

@media (max-width: 1500px) {
  .about-container {
    padding: 8vh 0rem 1rem;
  }

  .about-content {
    padding: 0;
  }

  .about {
    background-color: transparent;
  }

  .background {
    display: none;
  }
}

/* background animations */
@keyframes animate {
  0% {
    transform: translateY(0) rotate(0deg);
    opacity: 1;
    border-radius: 0;
  }
  100% {
    transform: translateY(-1000px) rotate(720deg);
    opacity: 0;
    border-radius: 50%;
  }
}

.background {
  position: fixed;
  /* width: 100vw; */
  /* height: 100vh; */
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  margin: 0;
  padding: 0;
  overflow: hidden;
  z-index: -1;
}
.background li {
  position: absolute;
  display: block;
  list-style: none;
  width: 20px;
  height: 20px;
  background: rgba(255, 255, 255, 0.2);
  animation: animate 19s linear infinite;
}

.background li:nth-child(0) {
  left: 49%;
  width: 148px;
  height: 148px;
  bottom: -148px;
  animation-delay: 1s;
}
.background li:nth-child(1) {
  left: 56%;
  width: 150px;
  height: 150px;
  bottom: -150px;
  animation-delay: 3s;
}
.background li:nth-child(2) {
  left: 66%;
  width: 172px;
  height: 172px;
  bottom: -172px;
  animation-delay: 4s;
}
.background li:nth-child(3) {
  left: 2%;
  width: 151px;
  height: 151px;
  bottom: -151px;
  animation-delay: 5s;
}
.background li:nth-child(4) {
  left: 6%;
  width: 181px;
  height: 181px;
  bottom: -181px;
  animation-delay: 18s;
}
.background li:nth-child(5) {
  left: 22%;
  width: 193px;
  height: 193px;
  bottom: -193px;
  animation-delay: 8s;
}
.background li:nth-child(6) {
  left: 28%;
  width: 137px;
  height: 137px;
  bottom: -137px;
  animation-delay: 1s;
}
.background li:nth-child(7) {
  left: 29%;
  width: 148px;
  height: 148px;
  bottom: -148px;
  animation-delay: 11s;
}
.background li:nth-child(8) {
  left: 7%;
  width: 111px;
  height: 111px;
  bottom: -111px;
  animation-delay: 27s;
}
.background li:nth-child(9) {
  left: 20%;
  width: 116px;
  height: 116px;
  bottom: -116px;
  animation-delay: 32s;
}
